<template>
	<view>
		<!-- <view>用户昵称：{{nickName?nickName:"没有获取到用户昵称"}}</view> -->
		<view class="">
			<image class="button_img" src="../../static/image/wx_login.png"></image>
		</view>
		<button class="buttonName" open-type="getUserInfo" @getuserinfo="bindGetUserInfo">一键登录</button>

		
	</view>
</template>

<script>
	 import {
		mapState,
		mapMutations
	} from 'vuex'; 

	export default {
		data() {
			return {
				nickName: "",
				modalName: '',
				buttonName: '重新获取授权'
			};
		},
		computed: {
			...mapState(['hasLogin', 'user', 'code'])
		},
		onShow() {},
		methods: {
			...mapMutations(['setCode', 'login']),
			bindGetUserInfo(res) {
				let that = this;
				uni.showLoading({
					title: '登录中...'
				});
				uni.getSetting({
					success: res => {
						console.log(res);
						if (res && res.authSetting && res.authSetting.hasOwnProperty("scope.userInfo") && res.authSetting[
								'scope.userInfo']) {
							//scope.userInfo字段存在
							if (res.authSetting["scope.userInfo"]) {
								//scope.userInfo:true为真
								uni.checkSession({
									fail: () => {
										uni.login({
											success: () => {
												that.setCode(loginRes.code);
											}
										})
									}
								});

								// 获取用户信息
								uni.getUserInfo({
									provider: "weixin",
									success: infoRes => {
										uniCloud.callFunction({
											name: "login",
											data: {
												'userInfo': infoRes.userInfo,
												'code': that.code,
											}
										}).then((res) => {
											uni.hideLoading();
											var res = res.result;
											console.log(res);
											if (res.statusCode == 0) {
												that.login(res.user);
												that.$storage.setStorageSync("USER", res.user);
												that.$storage.setStorageSync("TOKEN", res.token);
												uni.navigateBack({
													delta: -1
												})
											} else {
												uni.showToast({
													title: "登录失败，稍后再试！",
													duration: 3000
												})
											}
										});



										// uni.request({
										// 	url: that.$config.loginWx,
										// 	data: {
										// 		'userInfo': infoRes.userInfo,
										// 		'code': that.code,
										// 	},
										// 	method: 'POST',
										// 	success: (res) => {
										// 		if (res.statusCode == 200) {
										// 			that.login(res.data.data);
										// 			that.$storage.setStorageSync("USER", res.data.data);
										// 			uni.navigateBack({
										// 				delta: -1
										// 			})
										// 		} else {
										// 			uni.showToast({
										// 				title: "登录失败，稍后再试！",
										// 				duration: 3000
										// 			})
										// 		}

										// 	}
										// });
									}
								});
							} else {
								// scope.userInfo:false
								// 提醒用户开启授权
								uni.showToast({
									icon: "none",
									title: "请开启获取用户信息授权"
								});
								that.modalName = "authorize";
								that.buttonName = '重新获取授权';
							}
							console.log(that.modalName);
						} else {
							this.modalName = 'authorize';
						}
					}
				});
			},
		}
	};
</script>

<style>
	.button_img{
		width: 66%;
		margin:80px 17% 0 17% ;
	}
	.buttonName{
		background-color: #259c64;
		width: 40%;
		margin: 60px 30%;
		color: white;
	}
	
</style>